.footer {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 50px;
  left: 23px;
  justify-content: center;
  justify-content: space-around;
  align-items: center;
  background: linear-gradient(180deg, #66f8f8 0%, rgba(102, 248, 248, 0) 100%);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset;
  padding: 1rem 1rem 2rem;
  /* border-radius: 1rem 1rem 1rem 1rem; */
  transition: 0.3s;
}

.footer_list_item {
  list-style: none;
  margin-top: auto;
}
.footer_list_item_link {
  display: flex;
  align-items: center;
  color: darkblue;
  text-decoration: none;
  gap: 10px;
}
.footer_list {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.footer_list_item_link:hover {
  color: brown;
}
